							<view class="swiper-item"><image class="pic" src="../../static/banner1.jpg" mode="aspectFill"></image></view>
						</swiper-item>
						<swiper-item>
							<view class="swiper-item"><image class="pic" src="../../static/banner2.jpg" mode="aspectFill"></image></view>
						</swiper-item>
						<swiper-item>
							<view class="swiper-item"><image class="pic" src="../../static/banner3.jpg" mode="aspectFill"></image></view>
						</swiper-item>
					</swiper>
					<view class="wantBuyInfo">
						<view class="wantBuyUser">
							<view class="userImg">
								<image class="pic" src="../../static/user-head.png" mode=""></image>
							</view>
							<view class="userName singleHide">
								大客户
							</view>
						</view>
						<view class="describe multiHide">
							我想要购买图片上的这些东 西，大量现货的联系我
						</view>
						<view class="userAddress">
							<view class="addrImg">
								<image class="pic" src="../../static/icon_location.png" mode=""></image>
							</view>
							<view class="address multiHide">
								河南省郑州市管城回族 区升龙广场
							</view>
						</view>
						
					</view>
				</view>
				<view class="wantBuyOperation">
					<view class="del">
