</template>

<script>
	export default {
		data(){
			return {
				activeNav: 0, // 选中的头部导航
			}
		},
		methods:{
			// 切换头部导航
			changeNav(idx) {
				this.activeNav = idx
			},
			
			// 查看信息服务详情
			seeDetail(){
				uni.navigateTo({
					url: "./infoServerDetail"
				})
			},
			
			// 发布
			repair(){
				uni.navigateTo({
					url: "./repairServer"
				})
			},
		}
	}
</script>

<style lang="less">
	page{
		background-color: #f5f5f5;
	}
	
	.headNavigation {
		width: 750rpx;
		height: 92rpx;
		opacity: 1;
		background: #FFEBEB;
		display: flex;
	}
	
	.navItem {
		width: 50%;
		text-align: center;
		line-height: 92rpx;
		color: #999;
		font-size: 32rpx;
		position: relative;
	}
	
	.activeNav {
		color: #FF2D2D;
	}
	
	.activeNav::after {
		content: "";
		width: 32rpx;
		height: 8rpx;
		opacity: 1;
		background: #FF2D2D;
		border-radius: 12rpx;
		position: absolute;
		left: 50%;
		bottom: 8rpx;
		transform: translateX(-50%);
	}
	
	.wantBuyMain{
		padding: 20rpx 30rpx;
		.wantBuy{
			width: 690rpx;
			background: #ffffff;
			border-radius: 20rpx;
