			padding: 20rpx;
			.wantBuyImg{
				width: 300rpx;
				height: 300rpx;
				flex-shrink: 0;
				border-radius: 20rpx;
				overflow: hidden;
				margin-right: 20rpx;
				swiper-item{
					width: 100%;
					height: 100%;
					.swiper-item{
						width: 100%;
						height: 100%;
					}
				}
			}
			.wantBuyInfo{
				flex-shrink: 0;
				.wantBuyUser{
					display: flex;
					align-items: center;
					margin: 20rpx 0;
					.userImg{
						width: 68rpx;
						height: 68rpx;
						overflow: hidden;
						border-radius: 50%;
						margin-right: 20rpx;
					}
					.userName{
						font-size: 36rpx;
						color: #333;
						max-width: 180rpx;
					}
					
				}
				.call{
					width: 32rpx;
					height: 32rpx;
				}
				.describe{
					font-size: 28rpx;
					color: #333;
					margin-bottom: 40rpx;
					width: 336rpx;
					height: 70rpx;
				}
				.userAddress{
					display: flex;
					.addrImg{
						width: 28rpx;
						height: 28rpx;
						margin-right: 20rpx;
					}
					.address{
						font-size: 28rpx;
						color: #333;
						width: 280rpx;
						height: 70rpx;
					}
				}
			}
			.wantBuyOperation{
				display: flex;
				align-items: center;
				justify-content: flex-end;
				font-size: 24rpx;
				margin-top: 20rpx;
				.del{
					color: #999;
					margin-right: 10rpx;
				}
				.edit{
					color: #FF2D2D;
				}
			}
		}
	}
	
	.repairFixed{
		width: 100rpx;
		height: 100rpx;
		position: fixed;
		right: 30rpx;
		bottom: 80rpx;
	}
	
	.repairNull{
		margin: 80rpx auto;
		text-align: center;
		image{
			width: 600rpx;
			height: 600rpx;
		}
		.nullTips{
			font-size: 36rpx;
			color: #999;
			text-align: center;
		}
	}
</style>
